button,
fieldset,
input {
	all: unset;
}

.DialogOverlay {
	background-color: rgba(0, 0, 0, 0.44);
	position: fixed;
	inset: 0;
	animation: overlayShow 550ms cubic-bezier(0.16, 1, 0.3, 1);
}

.DialogContent {
	background-color: white;
	border-radius: 6px;
	box-shadow: hsl(206 22% 7% / 35%) 0px 10px 38px -10px, hsl(206 22% 7% / 20%) 0px 10px 20px -15px;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 90vw;
	max-width: 450px;
	max-height: 85vh;
	padding: 25px;
	animation: contentShow 550ms cubic-bezier(0.16, 1, 0.3, 1);
}
.DialogContent:focus {
	outline: none;
}

.DialogTitle {
	display: flex;
	align-items: center;
	gap: 10px;
	margin: 0;
	font-weight: 500;
	color: rgb(26, 21, 35);
	font-size: 23px;
}

.DialogDescription {
	margin: 20px 0 20px;
	color: rgb(111, 110, 119);
	font-size: 20px;
	line-height: 1.5;
}

.Button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	border-radius: 4px;
	padding: 0 15px;
	font-size: 15px;
	line-height: 1;
	font-weight: 500;
	height: 35px;
}

.IconButton {
	font-family: inherit;
	border-radius: 100%;
	height: 25px;
	width: 25px;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	color: rgb(86, 154, 255);
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
}
.IconButton:hover {
	background-color: rgba(86, 154, 255, 0.112);
}
.IconButton:focus {
	box-shadow: 0 0 0 2px rgb(86, 154, 255);
}

@keyframes overlayShow {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes contentShow {
	from {
		opacity: 0;
		transform: translate(-50%, -48%) scale(0.96);
	}
	to {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}
}
