.button {
	border: none;
	font-weight: 600;
	cursor: pointer;
	background: transparent;
	position: relative;
	padding: 0px;
	transition: filter 250ms ease-out 0s;
}

.button__label {
	display: flex;
	align-items: center;
	gap: 10px;
	position: relative;
	border-radius: 8px;
	color: rgb(86, 154, 255);
	font-size: 20px;
	padding: 16px 24px;
	background: rgb(250, 250, 250);
	transform: translateY(-4px);
	user-select: none;
	transition: transform 250ms ease-out 0s;
}

.button__shadow {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	border-radius: 8px;
	background: rgba(0, 0, 0, 0.1);
	transform: translateY(2px);
	transition: transform 250ms ease-out 0s;
}

.button__bg {
	position: absolute;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100%;
	border-radius: 8px;
	background: rgba(0, 0, 0, 0.1);
	transform: translateY(2px);
	transition: transform 250ms ease-out 0s;
	background: linear-gradient(
		to left,
		rgb(176, 176, 176) 0%,
		rgb(217, 217, 217) 8%,
		rgb(217, 217, 217) 92%,
		rgb(176, 176, 176) 100%
	);
}

.button:hover {
	filter: brightness(110%);
}

.button:hover .button__label {
	transform: translateY(-7px);
}

.button:hover .button__shadow:where(.button__shadow) {
	transform: translateY(6px);
}

.button:active .button__shadow:where(.button__shadow) {
	transform: translateY(1px);
	transition: transform 34ms ease 0s;
}

.button:active .button__label {
	transform: translateY(-2px);
	transition: transform 34ms ease 0s;
}
